<template>
  <main class="content container" v-if="$store.state.cartLoadComplete">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'list'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{$store.state.cartProducts.length}} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in cartProducts" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{cartTotal | numberFormat}} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" :disabled="cartProducts.length < 1" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
  <main class="content container" v-else>Загрузка корзины</main>
</template>

<script>
import CartItem from "@/components/CartItem";
import numberFormat from "@/helpers/numberFormat";
import {mapGetters} from "vuex";
export default {
  name: "CartPage",
  components: {CartItem},
  computed: {
    ...mapGetters(['cartProducts', 'cartTotal']),
  },
  filters: {
    numberFormat,
  },
}
</script>

<style scoped>

</style>
