import Vue from "vue";
import Vuex from 'vuex';
import {API_BASE_URL} from "@/config";

Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        cartProducts: [],
        cartProductsData: [],
        userKey: null,
        cartLoadComplete: false,
        orderInfo: null,
    },
    mutations: {
        updateCartProductAmount(state, {productId, amount}) {
            const cartProduct = state.cartProducts.find(item => item.productId === productId);
            if (cartProduct) {
                cartProduct.amount = amount;
            }
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        updateUserKey(state, userKey) {
            state.userKey = userKey;
        },
        cartLoadComplete(state, status) {
            state.cartLoadComplete = status;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map((item) => ({
                productId: item.product.id,
                amount: item.quantity,
                product: item.product,
            }));
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
    },
    getters: {
        cartProducts(state) {
            return state.cartProducts.map(item => ({
                ...item,
                product: {
                    ...item.product,
                    image: item.product.image.file.url,
                },
            }));
        },
        cartTotal(state, getters) {
            return getters.cartProducts.reduce((sum, item) => sum + item.product.price * item.amount, 0);
        },
        orderProducts(state) {
            return state.orderInfo ? state.orderInfo.basket.items.map(item => ({
                ...item,
                product: {
                    ...item.product,
                    image: item.product.image.file.url,
                },
            })) : [];
        },
        orderTotal(state, getters) {
            return getters.orderProducts.reduce((sum, item) => sum + item.price * item.quantity, 0);
        },
        orderInfo(state, getters) {
            return state.orderInfo;
        },
    },
    actions: {
        loadCart(context) {
            context.commit('cartLoadComplete', false);
            const query = new URLSearchParams({
                userAccessKey: context.state.userKey
            }).toString();

            return (new Promise(resolve => setTimeout(resolve, 3000))).then(() => {
                return fetch(`${API_BASE_URL}/api/baskets?${query}`)
                    .then(response => {
                        if (response.ok) {
                            return response.json()
                        }
                        throw new Error();
                    })
                    .then(data => {
                        if (!context.state.userKey) {
                            localStorage.setItem('userKey', data.user.accessKey);
                            context.commit('updateUserKey', data.user.accessKey);
                        }
                        context.commit('updateCartProductsData', data.items);
                        context.commit('syncCartProducts');
                    })
                    .catch()
                    .finally(() => context.commit('cartLoadComplete', true));
            });
        },
        addToCart(context, {productId, amount}) {
            const query = new URLSearchParams({
                userAccessKey: context.state.userKey,
            }).toString();
            return (new Promise(resolve => setTimeout(resolve, 3000))).then(() => {
                return fetch(`${API_BASE_URL}/api/baskets/products?${query}`, {
                    method: 'POST',
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    body: new URLSearchParams({productId, quantity: amount}).toString(),
                }).then(response => {
                    console.log(response);
                    if (response.ok) {
                        return response.json()
                    }
                    throw new Error();
                }).then(data => {
                    context.commit('updateCartProductsData', data.items);
                    context.commit('syncCartProducts');
                }).catch()
                .finally(() => context.commit('cartLoadComplete', true));
            });
        },
        updateCartProductAmount(context, {productId, amount}) {
            context.commit('updateCartProductAmount', {productId, amount});
            if (amount < 1) {
                return;
            }
            const query = new URLSearchParams({
                userAccessKey: context.state.userKey,
            }).toString();
            return (new Promise(resolve => setTimeout(resolve, 1000))).then(() => {
                return fetch(`${API_BASE_URL}/api/baskets/products?${query}`, {
                    method: 'PUT',
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    body: new URLSearchParams({productId, quantity: amount}).toString(),
                }).then(response => {
                    if (response.ok) {
                        return response.json()
                    }
                    throw new Error();
                }).then(data => {
                    context.commit('updateCartProductsData', data.items);
                }).catch(() => {
                    context.commit('syncCartProducts');
                }).finally(() => context.commit('cartLoadComplete', true));
            });
        },
        deleteFromCart(context, productId) {
            const query = new URLSearchParams({
                userAccessKey: context.state.userKey,
            }).toString();
            return (new Promise(resolve => setTimeout(resolve, 1000))).then(() => {
                return fetch(`${API_BASE_URL}/api/baskets/products?${query}`, {
                    method: 'DELETE',
                    headers: {'Content-Type': 'application/x-www-form-urlencoded'},
                    body: new URLSearchParams({productId}).toString(),
                }).then(response => {
                    if (response.ok) {
                        return response.json()
                    }
                    throw new Error();
                }).then(data => {
                    context.commit('updateCartProductsData', data.items);
                    context.commit('syncCartProducts');
                }).catch(() => {
                }).finally();
            });
        },
        loadOrder(context,orderId) {
            const query = new URLSearchParams({
                userAccessKey: context.state.userKey
            }).toString();
            return (new Promise(resolve => setTimeout(resolve, 2000))).then(() => {
                return fetch(`${API_BASE_URL}/api/orders/${orderId}?${query}`)
                    .then(response => {
                        if (response.ok) {
                            return response.json()
                        }
                        throw new Error();
                    })
                    .then(data => {
                        context.commit('updateOrderInfo', data);
                    })
                    .catch()
                    .finally();
            });
        },
    },
});
