<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'list'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="orderInfo">
        Заказ оформлен <span>№ {{orderInfo.id}}</span>
      </h1>
    </div>

    <section class="cart" v-if="orderInfo">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{orderInfo.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{orderInfo.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{orderInfo.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{orderInfo.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <OrderItems class="cart__orders" :cartProducts="orderProducts"/>
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{orderProducts.length}}</b> товара на сумму <b>{{orderTotal | numberFormat}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
    <section class="cart" v-else> Загрузка заказа </section>

  </main>

</template>

<script>
import OrderItems from "@/components/OrderItems";
import {mapGetters} from "vuex";
import numberFormat from "@/helpers/numberFormat";
export default {
  name: "OrderInfoPage",
  components: {
    OrderItems,
  },
  data() {
    return {
      Products: [],
      Total: 0,
    }
  },
  computed: {
    ...mapGetters(["orderInfo"]),
    orderProducts() {
      return this.orderInfo ? this.orderInfo.basket.items.map(item => ({
        ...item,
        product: {
          ...item.product,
          image: item.product.image.file.url,
          amount: item.quantity,
        },
      })) : [];
    },
    orderTotal() {
      return this.orderInfo ? this.orderInfo.totalPrice : 0;
    },
  },
  filters: {
    numberFormat,
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrder', this.$route.params.id);
  }
}
</script>

<style scoped>

</style>
