<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'list'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{cartProducts.length}} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" placeholder="Введите ваше полное имя" v-model="formData.name" :error="formError.name"/>
            <BaseFormText title="Адрес доставки" placeholder="Введите ваш адрес" v-model="formData.address" :error="formError.address"/>
            <BaseFormText title="Телефон" placeholder="Введите ваш телефон" type="phone" v-model="formData.phone" :error="formError.phone"/>
            <BaseFormText title="Email" placeholder="Введи ваш Email" type="email" v-model="formData.email" :error="formError.email"/>
            <BaseFormTextarea title="Комментарий к заказу" placeholder="Ваши пожелания" v-model="formData.comment" :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">

          <OrderItems class="cart__orders" :cartProducts="cartProducts"/>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{cartProducts.length}}</b> товара на сумму <b>{{cartTotal}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="ordering">
            <span v-if="ordering">Оформление...</span>
            <span v-else>Оформить заказ</span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import OrderItems from "@/components/OrderItems";
import {mapGetters} from "vuex";
import {API_BASE_URL} from "@/config";
export default {
  name: "OrderPage",
  components: {BaseFormText, BaseFormTextarea, OrderItems},
  data() {
    return {
      ordering: false,
      formData: {},
      formError: {},
      formErrorMessage: '',
    }
  },
  computed: {
    ...mapGetters(['cartProducts',  'cartTotal']),
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.ordering = true;
      const query = new URLSearchParams({
        userAccessKey: this.$store.state.userKey,
      }).toString();
      return (new Promise(resolve => setTimeout(resolve, 3000))).then(() => {
        return fetch(`${API_BASE_URL}/api/orders?${query}`, {
          method: 'POST',
          headers: {'Content-Type': 'application/x-www-form-urlencoded'},
          body: new URLSearchParams(this.formData).toString(),
        }).then(response => {
          if (response.ok) {
            return response.json()
          }
          let error = new Error('error');
          error.response = response;
          throw error;
        }).then((data) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', data);
          this.$router.push({name: 'orderInfo', params: {id: data.id}});
        }).catch((err) => {
          err.response.json().then((data)=>{
            this.formError = data.error.request || {};
            this.formErrorMessage = data.error.message;
          });
        })
        .finally(() => this.ordering = false);
      });
    },
  },
}
</script>

<style scoped>

</style>
