<template>
  <li class="cart__item product relative">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <ProductAmount class="product__counter form__counter" v-model.number="amount"/>

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
            @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
    <div class="overlay" v-show="productDeleting"></div>
  </li>

</template>

<script>
import ProductAmount from "@/components/ProductAmount";
import numberFormat from "@/helpers/numberFormat";
import {mapActions} from 'vuex';

export default {
  name: "CartItem",
  props: ['item'],
  components: {ProductAmount},
  data() {
    return {
      productDeleting: false,
    }
  },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
      },
    },
  },
  methods: {
    ...mapActions(['deleteFromCart']),
    deleteProduct(productId) {
      this.productDeleting = true;
      this.deleteFromCart(productId).finally(() => {
        this.productDeleting = false;
      });
    },
  },
  filters: {
    numberFormat,
  },
}
</script>

<style scoped>
  .relative {
    position: relative;
  }
  .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: grey;
    opacity: 0.3;
  }
</style>
