<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"></ProductFilter>
      <section class="catalog">
        <div v-if="!loadingComplete">Грузится....</div>
        <div v-if="loadingFailed">
          <div>Что-то пошло не так....</div>
          <div><button @click="loadProductsData">попробовать еще</button></div>
        </div>
        <ProductList :products="products"></ProductList>
        <BasePagination v-model="page" :total="productsTotal" :per-page="perPage"></BasePagination>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList";
import BasePagination from "@/components/BasePagination";
import ProductFilter from "@/components/ProductFilter";
import {API_BASE_URL} from "@/config";

export default {
  name: 'ProductListPage',
  components: {
    ProductFilter, ProductList, BasePagination,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      perPage: 3,
      productsData: null,
      loadingComplete: false,
      loadingFailed: false,
    }
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items.map(product => ({
        ...product,
        image: product.image.file.url
      })) : [];
    },
    productsTotal() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    page() {
      this.loadProductsData();
    },
    filterPriceFrom() {
      this.loadProductsData();
    },
    filterPriceTo() {
      this.loadProductsData();
    },
    filterCategoryId() {
      this.loadProductsData();
    },
    filterColorId() {
      this.loadProductsData();
    },

  },
  methods: {
    loadProductsData() {
      this.loadingComplete = false;
      this.loadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
      const query = new URLSearchParams({
        page: this.page,
        limit: this.perPage,
        categoryId: this.filterCategoryId,
        colorId: this.filterColorId,
        minPrice: this.filterPriceFrom,
        maxPrice: this.filterPriceTo,
      }).toString();
      fetch(`${API_BASE_URL}/api/products?${query}`)
          .then(response => {
            if (response.ok) {
              return response.json()
            }
            throw new Error();
          })
          .then(data => this.productsData = data)
          .catch(() => this.loadingFailed = true)
          .finally(()=> this.loadingComplete = true);
      }, 500);
    },
  },
  created() {
    this.loadProductsData();
  },
}
</script>

