import BaseFormField from "@/components/BaseFormField";
export default {
    name: "BaseFormText",
    props: ['title', 'placeholder', 'value', 'error'],
    components: {BaseFormField},
    computed: {
        valueData: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit('input', value);
            },
        },
    },
}
