<template>
<div>
    <button type="button" class="button" aria-label="Убрать один товар" @click="addAmount(-1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentAmount">

    <button type="button" class="button" aria-label="Добавить один товар" @click="addAmount(1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
</div>
</template>

<script>
export default {
  name: "ProductAmount",
  props: ['ProductAmount'],
  model: {
    prop: 'ProductAmount',
  },
  computed: {
    currentAmount: {
      get() {
        return this.ProductAmount
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
  methods: {
    addAmount(addValue) {
      const result = this.currentAmount + addValue;
      if (result > 0) {
        this.currentAmount = result;
      }
    },
  },
}
</script>

