<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" href="#" :to="{name:'product', params: {id: product.id}}">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <router-link href="#" :to="{name:'product', params: {id: product.id}}">
        {{ product.title }}
      </router-link>
    </h3>

    <span class="catalog__price">
    {{ product.price | numberFormat }} ₽
  </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="color in colors">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color.id" v-model="currentColorId">
          <span class="colors__value shdow" :style="`background-color: ${color.code};`"></span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";

export default {
  name: "ProductItem",
  props: ["product",],
  data() {
    return {
      "currentColorId": 0,
    }
  },
  computed: {
    colors() {
      return this.product.colors;
    },
  },
  filters: {
    numberFormat,
  },
}
</script>

<style scoped>
  .shdow {
    box-shadow: 0 0 2px #555;
  }
</style>
